@import url('https://fonts.googleapis.com/css2?family=Comic+Neue:ital,wght@0,400;0,700;1,300;1,400;1,700&family=Montserrat&display=swap');

body {
  margin: 0;
  font-family: 'Comic Neue', cursive;
}

.container {
  font-family: 'Comic Neue', cursive;
}

footer {
  color: whitesmoke;
  font-weight: bold;
  font-size: 20px;
  background-color: black;
	background-image: url('./assets/images/bg-footer.png');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.navbar {
  color: #C60021;
  background-color: white;
  font-weight: bold;
  font-family: 'Comic Neue', cursive;
}

.centrar-nav-items{
  margin-left: 30px;
  margin-right: 30px;
  margin-top: auto;
  margin-bottom: auto;
}

.navbar-brand {
  max-width: 100;
  display: flex;
  align-items: center;
}

.navbar-brand img {
  max-width: 100;
  padding: 7px 14px;
}

nav a{
  color: whitesmoke;
  font-weight: bold;
}

.hero{
	position: relative;
	width: 100%;
	height: calc(100vh - 0px);
	background-color: white;
	background-size: cover;
	background-position: center center;
	transition: all .1s ease-out;
	background-image: url('./assets/images/hero-1.jpg');
	background-attachment: fixed;
  animation: hero 28s infinite linear;
}

.hero-contenido{
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	color: white;
	background-color:  rgba(198, 0, 33, 0.7);
	display: flex;
	flex-direction: column;
	justify-content: center;
  align-items: center;
  font-family: 'Comic Neue', cursive;
}

.hero-contenido img{
	max-width: 100%;
	max-height: 100%;
	padding: 0;
	margin: 0;
	padding-bottom: 30px;
}

.hero-contenido a{
	text-decoration: none;
	color: white;
	padding: 9px 20px;
	border: 1px solid white;
	text-transform: uppercase;
	transition: all .3s ease-in-out;
}

.hero-contenido a:hover{
	background-color: white;
	color: darkslategrey;
}

@keyframes hero{
	0%{
		background-image: url('./assets/images/hero-1.jpg');
	}
	33%{
		background-image: url('./assets/images/hero-1.jpg');
	}


	34%{
		background-image: url('./assets/images/hero-2.jpg');
	}
	66%{
		background-image: url('./assets/images/hero-2.jpg');
	}


	67%{
		background-image: url('./assets/images/hero-3.jpg');
	}
	100%{
		background-image: url('./assets/images/hero-3.jpg');
	}
}

.home-envios{
	position: relative;
	width: 100%;
	background-color: white;
	background-size: cover;
	background-position: center center;
	transition: all .1s ease-out;
	background-image: url('./assets/images/stock01.jpg');
	background-attachment: fixed;
}

.home-envios-contenido{
	margin: 0;
	padding-top: 30px;
	width: 100%;
	height: 100%;
	color: white;
	background-color:  rgba(198, 0, 33, 0.7);
	display: flex;
	flex-direction: column;
	justify-content: center;
  align-items: center;
  font-family: 'Comic Neue', cursive;
}

.home-content-image {
  border-radius: 10px;
  width: 100%;
  box-shadow: 0px 0px 3px 1px #ccc;
  margin-top: 50px;
  margin-bottom: 50px;
}

.home-content-image-tcg {
  border-radius: 10px;
  width: 100%;
  max-width: 600px;
  max-height: 600px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
}

.carousel {
  max-width: 700px;
  text-align: center;
  align-items: center;
}

.footer-content-image {
  border: 0px;
  border-radius: 10px;
  height: 100%;
  max-height: 300px;
  width: 100%;
  max-width: 300px;
  margin-top: 30px;
}

.home-content {
  margin-top: 30px;
}

.text-animation { 
            width: 100%; 
            top: 30%;  
            left: 30%; 
            border-bottom: 5px solid white; 
            overflow: hidden; 
            animation: animate 5s linear forwards; 
        }
  
        @keyframes animate { 
            0% { 
                width: 0px; 
                height: 0px; 
            } 
            30% { 
                width: 50%; 
                height: 0px; 
            } 
            60% { 
                width: 100%; 
                height: 100%; 
            } 
        }
        
.red-text {
  color: #C60021;
}

.g-card {
  margin: 20px;
}

.g-card-image {
  border-radius: 10px;
  width: 300px;
  height: 480px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.g-card-info {
  margin-top: 10px;
  min-height: 100px;
}

.g-card-title {
  font-size: 24px;
  margin: 0px;
}

.g-card-sub-title {
  font-size: 16px;
  margin: 0px;
}

.about-profile {
  border-radius: 10px;
  width: 280px;
  height: 460px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.about-skill {
  border-radius: 10px;
  max-width: 200px;
  max-height: 200px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.about-skill-tittle {
  font-size: 50px;
  font-weight: bold;
  margin-bottom: 10px;
}

.success-msg {
  color: #57A773;
  padding: 10px 15px;
}

.err-msg {
  color: #EE6352;
  padding: 10px 15px;
}

.facebook-icon {
  color: white;
  width: 60px;
  height: 60px;
  margin-left: 10px;
}
.facebook-icon:hover {
  color: lightsteelblue;
}